/* eslint-dasabled */
$(".action-btn").on("click", function(e) {
  e.preventDefault();
  const offset = $(".main-header.fixed-top").height() || 0;
  const to = $(this).attr("href");
  $("html,body").animate({ scrollTop: $(to).offset().top - offset }, "slow");
  $(".main-nav").removeClass("active");
  $(".toggle").removeClass("active");
});

$(".collapse-item__header").on("mouseenter mouseleave", function(e) {
  e.preventDefault();
  if (
    $(this)
      .parent()
      .hasClass("active")
  ) {
    return;
  }
  $(".collapse-item__body").slideUp();
  $(".collapse-item").removeClass("active");
  $(this)
    .parent()
    .addClass("active")
    .find(".collapse-item__body")
    .slideToggle();
});

$(".toggle").on("click", function(e) {
  e.preventDefault();
  $(this).toggleClass("active");
  $(".main-nav").toggleClass("active");
});
